import { Route, Routes } from "react-router-dom";
import HomePage from "../homePage/homePage";
import Page404 from "../page404/page404";
import "./routingManager.css";
import Contacs from '../contacs/contacs';
import ExchangePage from "../exchangePage/exchangePage";

function RoutingManager(): JSX.Element {
    return (
        <div className="routingManager">
			<Routes>
                <Route path="/" element={<HomePage/>}/>
                <Route index element={<HomePage/>}/>
                <Route path="/contacts" element={<Contacs/>}/>
                <Route path="/exchange" element={<ExchangePage/>}/>

                <Route path="*" element={<Page404/>}/>
            </Routes>
        </div>
    );
}

export default RoutingManager;
