import { AppBar, Box, Divider, IconButton, Menu, MenuItem, Toolbar, Tooltip, Typography } from "@mui/material";
import { useLocation, useNavigate } from "react-router-dom";
import "./myHeader.css";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import HomeIcon from '@mui/icons-material/Home';
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import PersonSearchIcon from '@mui/icons-material/PersonSearch';
import MenuIcon from '@mui/icons-material/Menu';
import { useState } from "react";




function MyHeader(): JSX.Element {
    const navigate=useNavigate();
    const location=useLocation();

    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);
    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    const menuClick=(path:string)=>{
        handleClose();
        navigate(path);
    }

    return (
        <div className="myHeader">
			<AppBar position="static" sx={{bgcolor:"primary.main"}}>
                <Toolbar>

                <Box display={{xs:'block', sm:'none'}}>
                        <IconButton
                            color="inherit"
                            onClick={handleClick}
                        >
                            <MenuIcon/>
                        </IconButton>
                    </Box>
                    <Menu
                        open={open}
                        anchorEl={anchorEl}
                        onClose={handleClose}
                    >
                        <MenuItem onClick={()=>menuClick("/")}><HomeIcon/> </MenuItem>
                        {(location.pathname.match("/exchange")==null)&&(<Divider/>)}
                        {(location.pathname.match("/exchange")==null)&&(<MenuItem onClick={()=>menuClick("/exchange")}><CurrencyExchangeIcon/> </MenuItem>)}
                        {(location.pathname.match("/contacts")==null)&&(<Divider/>)}
                        {(location.pathname.match("/contacts")==null)&&(<MenuItem onClick={()=>menuClick("/contacts")}><PersonSearchIcon/> </MenuItem>)}
                    </Menu>

                <Box display={{xs:'none',sm:'block'}}>
                        <Tooltip title={" "}>
                            <IconButton
                                color="inherit"
                                onClick={()=>navigate("/")}
                            >
                                <HomeIcon/>
                            </IconButton>
                        </Tooltip>

                        {(location.pathname.match("/exchange")==null)&&
                        (<Tooltip title={" "}>
                            <IconButton
                                color="inherit"
                                onClick={()=>navigate("/exchange")}
                            >
                                <CurrencyExchangeIcon/>
                            </IconButton>
                        </Tooltip>)}

                        {(location.pathname.match("/contacts")==null)&&
                            (<Tooltip title={" "}>
                                <IconButton
                                    color="inherit"
                                    onClick={()=>navigate("/contacts")}
                                >
                                    <PersonSearchIcon/>
                                </IconButton>
                            </Tooltip>)}
                    </Box>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                         
                    </Typography>
                    
                    <Tooltip title={""}>
                        <IconButton
                            color="inherit"
                            onClick={()=>navigate(-1)}
                        >
                            <ArrowBackIcon/>
                        </IconButton>
                    </Tooltip>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default MyHeader;
