import { Autocomplete, Box, Button, ButtonGroup, CircularProgress, IconButton, Select, TextField, Typography } from "@mui/material";
import axios from "axios";
import { SyntheticEvent, useState } from "react";
import { currencies } from "../../Moduls/currencies";
import "./exchangePage.css";
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import { CompareArrows, ConstructionOutlined } from "@mui/icons-material";
import globals from "../../utils/globals";
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import ClearIcon from '@mui/icons-material/Clear';
import { textAlign } from "@mui/system";
import { Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import ExchangeDetails from "../../Moduls/exchangeDetails";
import MyExchange from "../../myProps/myExchange/myExchange";


function ExchangePage(): JSX.Element {
    const myAxios=axios.create();
    const [from, setFrom]=useState<string|null>(null);
    const [to,setTo]=useState<string|null>(null);
    const [amount,setAmount]=useState('');
    const {register,handleSubmit, formState:{errors}} = useForm<ExchangeDetails>();
    const [details,setDetails]=useState(new ExchangeDetails());
    const [loading,setLoading]=useState(false);
    const [show,setShow]=useState(false);
    const [fromError, setFromError]=useState(false);
    const [toError,setToError]=useState(false);

    const handleChange=(sender:SyntheticEvent)=>{
        const value=(sender.target as HTMLInputElement).value;
        setAmount(value);
    }
    // http://localhost:8080/money/convert/usd/ils/100s

    const getResult=()=>{
        
        if(from!+null&&to!=null){
            setLoading(true);
            setShow(false);
            setFromError(false);
            setToError(false);
            myAxios.get(globals.url.convert+from+"/"+to+"/"+amount)
            .then(response=>{
                if(response.status===200){
                    setLoading(false);
                    setShow(true);
                    setDetails(response.data);
                }else{
                    (console.log(response.data));
                }
            })
            .catch(error=>{
                console.log(error);
            })
        }else{
            setFromError(from==null);
            setToError(to==null);
        }
    }
    const switchCurr=()=>{
        const temp=to;
        setTo(from);
        setFrom(temp);
    }
    const resetAll=()=>{
        setAmount('');
        setFrom(null);
        setTo(null);
        setShow(false);
        setLoading(false);
        setFromError(false);
        setToError(false);
    }
    return (
        <div className="exchangePage">
            <Typography sx={{width:'70%',justifyContent:'center',display:'inline-block'}} variant="h4">            </Typography>
            <Form onSubmit={handleSubmit(getResult)}>
                <Box sx={{justifyContent:'center', margin:5, display:'inline-flex'}}>
                    <br/><br/>
                    <Autocomplete
                        disablePortal
                        options={currencies}
                        sx={{width:120}}
                        renderInput={(params)=><TextField{...params} error={fromError} label="-"/>}
                        value={from}
                        onChange={(event:any,newValue:string|null)=>{setFrom(newValue);setFromError(newValue==null)}}
                        
                    />
                    &nbsp;
                    <IconButton onClick={switchCurr}><CompareArrows sx={{fontSize:35}}/></IconButton>
                    &nbsp;
                    <Autocomplete
                        disablePortal
                        options={currencies}
                        sx={{width:120}}
                        renderInput={(params)=><TextField{...params} error={toError} label="-"/>}
                        value={to}
                        onChange={(event:any,newValue:string|null)=>{setTo(newValue);setToError(newValue==null)}}
                    />
                </Box>
                
                {(fromError||toError)&&(<Typography variant="body1">   </Typography>)}
                <br/>
                {!(fromError||toError)&&(<br/>)}
                <TextField
                    label=""
                    type="number"
                    variant="outlined"
                    name="amount"
                    value={amount}
                    sx={{width:300}}
                    {...register("amount",{onChange:(e)=>handleChange(e)})}
                    required
                />
                <br/><br/>
                    <Button 
                        type="submit" 
                        variant="contained" 
                        sx={{width:230, justifyContent:'center', height:33}}
                    >
                         <CurrencyExchangeIcon sx={{marginRight:1}}/>
                    </Button>
                    <Button 
                        type="reset" 
                        color="error" 
                        variant="contained" 
                        size="small"
                        onClick={resetAll}
                    >
                        <ClearIcon/>
                    </Button>
            </Form>
            <br/><br/>
            {(loading)&&(<CircularProgress/>)}
            {(show)&&(<MyExchange details={details}></MyExchange>)}
        </div>
    );
}

export default ExchangePage;
